import "./App.css";
import "bootstrap/dist/css/bootstrap.min.css";
import Home from "./Pages/Home";
import About from "./Pages/About";
import Blog from "./Pages/Blog";
import menuu from "./config/menu.json";
import React, { useState } from "react";
import BlogDetail from "./Pages/BlogDetail";
import MUISwitch from "./components/materialUI/MUISwitch";
import { Container, Navbar, Nav } from "react-bootstrap";
import { useRive, useStateMachineInput } from "rive-react";
import { Route, Routes, Link } from "react-router-dom";
function App() {
  const [darkMode, setDarkMode] = useState(false);
  const STATE_MACHINE_NAME = "state";
  const INPUT_NAME = "toggle";

  function darkModeSet() {
    // onClickInput.fire();
    darkMode ? setDarkMode(false) : setDarkMode(true);
    return true;
  }
  const { RiveComponent, rive } = useRive({
    src: "/1.riv",
    stateMachines: STATE_MACHINE_NAME,
    autoplay: true,
  });
  const onClickInput = useStateMachineInput(rive, STATE_MACHINE_NAME, INPUT_NAME);

  return (
    <>
      <div className="main">
        <Navbar bg={darkMode ? `dark` : `Light`} variant={darkMode ? `dark` : `Light`}>
          <Container>
            <Link className={darkMode ? `darkmodeText` : `lightmodeText`} to="/">
              <Navbar.Brand className={darkMode ? `darkmodeText` : `lightmodeText`}>Site Name</Navbar.Brand>
            </Link>
            <Nav className="me-auto">
              {menuu.map((item, index) => (
                <Nav.Link key={index}>
                  <Link className={darkMode ? `darkmodeText` : `lightmodeText`} to={item.link}>
                    {item.name}
                  </Link>
                </Nav.Link>
              ))}
            </Nav>
            <div style={{ height: "50px" }}>
              <MUISwitch
                onClick={() => {
                  darkModeSet();
                }}
                defaultChecked
              />
            </div>
          </Container>
        </Navbar>
        <Routes>
          <Route path="/" element={<Home darkMode={darkMode} />} />
          <Route path="/blog" element={<Blog darkMode={darkMode} />}>
            <Route path=":id" element={<BlogDetail darkMode={darkMode} />} />
          </Route>
          <Route path="/about" element={<About darkMode={darkMode} />} />
        </Routes>
      </div>
    </>
  );
}

export default App;
